hr {
    border: 1px solid #6f47cc;
    font-size: 2rem;
}

.navbar {
    /* height: 3rem; */
    background-color: #36213e;
}

.navbar .title {
    color: white;
    font-size: 2rem;
    padding: 0.5rem;
}

.card {
    border: 1px solid #63768d;
    border-radius: 3px;
    padding: 1rem;
    margin: 1rem;
}

.card .header {
    font-size: 1.5rem;
}

.exerciseContainer > * {
    flex-grow: 1;
}

.bpmContainer, .scaleContainer {
    display: flex;
    gap: 1rem;
}

.scaleContainer {
    flex-grow: 2;
}

.bpmContainer > input {
    flex-grow: 1;
}